body {
  margin: 0;
  padding: 0;
}
nav {
  width: 19%;
}
hr {
  border-color: #afafaf54;
}
button {
  color: white;
  background-color: #35342db8;
  margin-right: 6px;
  border-radius: 13px;
}
p {
  margin-bottom: 10px;
}
.Search {
  text-align: center;
  color: white;
  align-self: baseline;

  width: 100%;
  background-color: #35342db8;
  height: 81px;
}
::placeholder {
  color: #ffffff47;
}
.Input {
  color: white;
  width: 19%;
  height: 22px;
  background-color: #0000004f;
}
.App {
  text-align: center;
}
body,
html {
  widows: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
h1 {
  display: flex;
  align-items: center;
}
#root {
  height: 100%;
}
td {
  border: 2px solid hsla(0, 0%, 68.6%, 0.33);
}
.MainContent {
  padding: 10px;
  width: 100%;
  height: 100%;
  border: 2px solid #000;
  background-image: url(https://images.pexels.com/photos/6754/road-nature-lines-country.jpg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940);
  background-size: cover;
}
.MainContent2 {
  display: flex;
  align-items: center;
  width: 100%;
  height: 77%;
}
.Provinces {
  color: #fff;
  width: 91%;
  height: 20px;
  margin-bottom: 10px;
  border: 1px solid #fff;
  padding: 0.25rem;
  background-color: rgba(0, 0, 0, 0.31);
}
.MyLocation {
  background-color: #908e8e5e;
  color: #fff;
  width: 91%;
  height: 20px;
  margin-bottom: 10px;
  border: 1px solid #fff;
  padding: 0.25rem;
}
table {
  text-align: left;
}
.DisplayWeather {
  justify-content: center;
  align-items: center;
  display: flex;
  flex-grow: 1;
}
.WeatherContent {
  text-align: center;
  width: 56%;
  background-color: rgba(53, 52, 45, 0.72);
  color: #fff;
  padding: 1rem;
}

@media only screen and (max-width: 600px) {
  body,
  html {
    height: 110%;
  }
  nav {
    align-self: flex-end;
    margin-top: 60px;
    width: auto;
    margin: 9px;
    display: flex;
    flex-grow: 1;
    flex-wrap: wrap;
  }
  .DisplayWeather {
    display: flex;
    margin-top: 9px;
  }
  .MainContent2 {
    align-items: baseline;
    flex-wrap: wrap;
    width: fit-content;
    height: 85%;
  }
  .WeatherContent {
    flex-grow: 1;
  }
  .Provinces.MyLocation {
    height: 38px;
  }
}
